.container {
    height: 300vh;
    position: relative;
}

.container .sticky {
    position: sticky;
    overflow: hidden;
    top: 0;
    height: 100vh;
}

.container .sticky .el {
    width: 100%;
    height: 100%;
    top: 0;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container .sticky .el .imageContainer {
    position: relative;
    width: 25vw;
    height: 25vh;
}

.container .sticky .el .imageContainer img {
    object-fit: cover;
}

.container .sticky .el:nth-of-type(2) .imageContainer {
    top: -30vh;
    left: 5vw;
    width: 35vw;
    height: 30vh;
}

.container .sticky .el:nth-of-type(3) .imageContainer {
    top: -10vh;
    left: -25vw;
    width: 20vw;
    height: 45vh;
}

.container .sticky .el:nth-of-type(4) .imageContainer {
    left: 27.5vw;
    width: 25vw;
    height: 25vh;
}

.container .sticky .el:nth-of-type(5) .imageContainer {
    top: 27.5vh;
    left: 5vw;
    width: 20vw;
    height: 25vh;
}

.container .sticky .el:nth-of-type(6) .imageContainer {
    top: 27.5vh;
    left: -22.5vw;
    width: 30vw;
    height: 25vh;
}

.container .sticky .el:nth-of-type(7) .imageContainer {
    top: 28.5vh;
    left: 30vw;
    width: 25vw;
    height: 25vh;
}
